/* AnimatedLines CSS Template */
.AnimatedLines_horizontalLineTop__2lScs {
    position: absolute;
    border-bottom: 4px solid #D4D4D4;
    animation: AnimatedLines_horizontalLineTopA__3H9u9 2s linear forwards;
    top: 0;
    left: calc(50% - 4px)
}

@keyframes AnimatedLines_horizontalLineTopA__3H9u9 {
    from {
        left: calc(50% - 4px);
        width:0;
    }
    to {
        left:0;
        width:100%;
    }
}

/* AnimatedLines CSS Template */
.AnimatedLines_horizontalLineBottom__34rSB {
    position: absolute;
    border-bottom: 4px solid #D4D4D4;
    animation: AnimatedLines_horizontalLineBottomA__6WXiO 2s linear forwards;
    bottom: 0;
    left: calc(50% - 4px)
}

@keyframes AnimatedLines_horizontalLineBottomA__6WXiO {
    from {
        left: calc(50% - 4px);
        width:0;
    }
    to {
        left:0;
        width:100%;
    }
}

.AnimatedLines_verticalLineLeft__NzY8G {
    position: absolute;
    border-right: 4px solid #D4D4D4;
    text-align: center;
    animation: AnimatedLines_verticalLineLeftA__1IHGG 2s linear forwards;
    left: calc(100% - 4px);
    z-index: 15;
}

@keyframes AnimatedLines_verticalLineLeftA__1IHGG {
    from {
        height: 0;
    }
    to {
        height: 100%;
    }
}


.AnimatedLines_verticalLineRight__22Zdo {
    position: absolute;
    border-right: 4px solid #D4D4D4;
    text-align: center;
    animation: AnimatedLines_verticalLineRightA__3BL66 2s linear forwards;
    right: calc(100% - 4px);
    z-index: 15;
}

@keyframes AnimatedLines_verticalLineRightA__3BL66 {
    from {
        height: 0;
    }
    to {
        height: 100%;
    }
}

@media screen and (max-width: 736px){
    .AnimatedLines_verticalLineLeft__NzY8G{
        display: none;
    }
    .AnimatedLines_verticalLineRight__22Zdo{
        display: none;
    }
}
